const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    car: cc.Node = null;

    @property(cc.Node)
    topPoint: cc.Node = null;
    @property(cc.Node)
    bottomPoint: cc.Node = null;
    @property(cc.Node)
    leftPoint: cc.Node = null;
    @property(cc.Node)
    rightPoint: cc.Node = null;

    @property(cc.Float)
    movementSpeed: number;

    onLoad () {
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyPressed, this);
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onKeyPressed(event){
        switch(event.keyCode) {
            case cc.macro.KEY.a:
                console.log("LEFT");
                this.car.setPosition(this.car.position.x - this.movementSpeed, this.car.position.y);
                if(this.car.position.x < this.leftPoint.position.x){
                    this.car.setPosition(this.leftPoint.position.x, this.car.position.y);
                }
                break;

            case cc.macro.KEY.d:
                console.log("RIGHT");
                this.car.setPosition(this.car.position.x + this.movementSpeed, this.car.position.y);
                if(this.car.position.x > this.rightPoint.position.x){
                    this.car.setPosition(this.rightPoint.position.x, this.car.position.y);
                }
                break;

            case cc.macro.KEY.w:
                console.log("UP");
                this.car.setPosition(this.car.position.x, this.car.position.y + this.movementSpeed);
                if(this.car.position.y > this.topPoint.position.y){
                    this.car.setPosition(this.car.position.x, this.topPoint.position.y);
                }
                break;

            case cc.macro.KEY.s:
                console.log("DOWN");
                this.car.setPosition(this.car.position.x, this.car.position.y - this.movementSpeed);
                if(this.car.position.y < this.bottomPoint.position.y){
                    this.car.setPosition(this.car.position.x, this.bottomPoint.position.y);
                }
                break;

        }
    }

    moveLeft(){
        
    }

    moveRight(){
        
    }

    moveUp(){

    }

    moveDown(){
        this.car.setPosition(this.car.position.x, this.car.position.y - this.movementSpeed);
        if(this.car.position.y < this.topPoint.position.y){
            this.car.setPosition(this.car.position.x, this.bottomPoint.position.y);
        }
    }
}
